<template>
  <div class="hero" :style="Style">
    <div class="container">
      <Header :color="color" :searchBack="search" />
    </div>
    <white-comp v-if="page !== 'plans'" />
    <sec-1-end v-if="page === 'home'" />
    <start-sec-1-end v-if="page === 'start'" />
    <PlansSec1 v-if="page === 'plans'" />
  </div>
</template>

<script>
import Header from "./Header.vue";
import WhiteComp from "./WhiteComp.vue";
import Sec1End from "./Sec1End.vue";
import PlansSec1 from "./PlansSec1.vue";
import StartSec1End from "./StartSec1End.vue";

export default {
  name: "Hero",
  components: {
    Header,
    WhiteComp,
    Sec1End,
    StartSec1End,
    PlansSec1,
  },
  props: {
    color: {
      default: "#515e7b",
      type: String,
    },
    rmimg: {
      default: false,
      type: Boolean,
    },
    comp: {
      type: String,
    },
    page: String,
    search: String,
  },
  computed: {
    Style() {
      return {
        "--background-color": this.color,
        "--img": this.rmimg
          ? "linear-gradient( to bottom," + this.color + "20%,  #1b7ad0)"
          : "url(https://img.fortawesome.com/349cfdf6/home-scene-winter.svg)",
      };
    },
  },
};
</script>

<style scoped>
.hero {
  padding: 10px 0 100px 0;
  background: var(--background-color);
  background-image: var(--img);
  background-size: cover;
}
</style>
