import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import './css/style.css';
import './filters';

Vue.config.productionTip = false;
Vue.directive('font', {
  bind: (el, binding) => {
    return (el.style.fontSize = `${binding.value}px`);
  },
});

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount('#app');
