<template>
  <div class="search" :style="Style">
    <div class="box">
      <p>Search icons...</p>
      <i class="fa fa-search" aria-hidden="true"></i>
    </div>
  </div>
</template>

<script>
export default {
  name: "Search",
  props: {
    color: {
      default: "#636e89",
      type: String,
    },
  },
  computed: {
    Style() {
      return {
        "--back-color": this.color,
      };
    },
  },
};
</script>

<style scoped>
.box {
  padding: 10px;
  height: 40px;
  width: 182px;
  border-radius: 16px;
  background: var(--back-color);
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}
p,
i {
  color: #dfe2e7;
  font-size: 14px;
  margin: 0;
}
</style>
