<template>
  <div class="header" :style="Style">
    <nav class="navbar navbar-expand-lg navbar-light">
      <div class="container-fluid">
        <p class="navbar-brand"><Search :color="searchBack" /></p>
        <button
          class="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarToggleExternalContent"
          aria-controls="navbarToggleExternalContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
          @click="toggle"
        >
          <span class="navbar-toggler-icon"></span>
        </button>
        <div
          class="collapse navbar-collapse"
          :class="hide"
          id="navbarNavAltMarkup"
        >
          <div class="navbar-nav">
            <router-link
              class="nav-link active"
              aria-current="page"
              to="/"
              exact
              >Home</router-link
            >
            <router-link to="/start" class="nav-link"> Start</router-link>
            <router-link to="/plans" class="nav-link">Plans</router-link>
          </div>
          <span class="nav-link span">sign in </span>
        </div>
      </div>
    </nav>
  </div>
</template>

<script>
import Search from "./Headercomp/Search.vue";

export default {
  components: {
    Search,
  },
  name: "Header",
  data: () => {
    return {
      hide: "",
    };
  },
  methods: {
    toggle() {
      let btn = document.querySelector(".navbar-collapse");
      btn.classList.toggle("show");
    },
  },
  props: {
    color: {
      type: String,
    },
    searchBack: String,
  },
  computed: {
    Style() {
      return {
        "--background-color": this.color,
      };
    },
  },
};
</script>

<style scoped>
.navbar {
  background: var(--background-color);
}
.nav-link {
  text-decoration: none;
  color: #dbdadae6 !important;
  font-weight: bold;
}
.navbar-brand {
  margin: 0;
}

.navbar-nav {
  display: flex;
}
.span {
  margin: 0 0 0 auto;
  position: relative;
  cursor: pointer;
}
.span::before {
  content: "sign in";
  width: 40px;
  height: 20px;
  background: black;
  color: white;
  position: absolute;
  top: 10px;
  left: 0px;
  font-size: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  opacity: 0;
  transition: 0.3s;
}
.span::after {
  content: "";
  border: 6px solid;
  border-color: transparent transparent transparent black;
  position: absolute;
  top: 14px;
  left: 40px;
  opacity: 0;
  transition: 0.3s;
}

.span:hover::before,
.span:hover::after {
  opacity: 1;
}
.span:hover::before {
  left: -30px;
}
.span:hover::after {
  left: 10px;
}
@media only screen and (max-width: 1000px) {
  .span {
    padding: 0;
  }
  .span:hover::before,
  .span:hover::after {
    opacity: 0;
  }
}
</style>
