<template>
  <button :class="color" :style="CssProps">{{ cont }}</button>
</template>

<script>
export default {
  name: "Button",
  props: {
    cont: String,
    color: {
      default: "blue",
      type: String,
    },
    size: {
      default: 70,
      type: Number,
    },
  },
  computed: {
    CssProps() {
      return {
        "--button-height": this.size + "px",
      };
    },
  },
};
</script>

<style scoped>
button {
  height: var(--button-height);
  width: fit-content;
  padding: 0 30px;
  font-size: 20px;
  border-radius: 5px;
  box-shadow: 3px 0 5px black;
  border: none;
}
.blue {
  background: #183153;
  color: white;
}
.blue:hover {
  background: #101f35;
}
.lightblue {
  background: #1c7ed6;
  color: white;
}
.lightblue:hover {
  background: #155fa0;
}
.name {
  background: green;
}
.yellow {
  background: #ffd43b;
  color: black;
  font-size: 14px;
  font-weight: bold;
}
.yellow:hover {
  background: #d6b332;
}

.green {
  background: #20c997;
  font-weight: bold;
  color: white;
}
.green:hover {
  background: #1bac80;
}
</style>
